.open-source-project-container {
    background-color: #c23231;
    min-height: calc(52vh);
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;

    h1 {
        margin-top: 40px;
        color: white;
        width: 350px;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        color: black;
        text-align: center;
        font-family: "Fredoka", sans-serif;
        letter-spacing: -1px;
        font-size: 24px;
        border-radius: 40px;
        box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

        @media screen and(max-width:992px) {
            margin-top: 70px !important;
        }

        .icon {
            display: flex;
            width: 60px;
        }
    }

    .open-source-project {
        .open-source-project-row {
            margin-top: 0px;
            margin-bottom: 0px;
            align-items: center;
            justify-content: center;
            display: flex;
            margin-top: 30px;
            margin-bottom: 60px;

            .open-source-project-item {
                height: 320px;
                margin-top: 40px;
                margin-bottom: 40px;
                padding: 0px 20px !important;

                .item-container {
                    background-color: white;
                    box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                    -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                    -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

                    .project-image-container {
                        width: 100%;
                        height: 150px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-bottom: 1px solid lightgray;

                        img {
                            height: 70px;
                            width: auto;
                            object-fit: contain;
                        }
                    }

                    .project-key {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1.5rem;
                        font-weight: bolder;
                        height: 50px;
                        margin-top: 10px;
                    }

                    .project-description {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1rem;
                        height: 75px;
                        padding: 0px 20px;
                        display: -webkit-box;
                        max-width: 100%;
                        margin: 0 auto;
                        -webkit-line-clamp: 3;
                        /* autoprefixer: off */
                        -webkit-box-orient: vertical;
                        /* autoprefixer: on */
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }

                    .project-link {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        font-size: 1rem;
                        height: 80px;

                        a {
                            outline: none !important;
                            width: 84%;
                            font-family: inherit;
                        }
                    }
                }
            }
        }
    }
}
